import classnames from 'classnames';
import React, { useEffect, useState } from 'react';
import { useRecoilState } from 'recoil';
import data from '../core/data';
import EventEmitter, { Event } from '../core/EventEmitter';
import { isShowSpeedmateState } from '../core/store';
import ZZAudioProgram from './ZZAudioProgram';
let sentenseCnt = 0;
let speed = 0.7;
const isYTMode = false;
const cntSetObj: any = {
    '0.7': 10,
    '0.8': 10,
    '0.9': 10,
    '1.0': 60,
    '1': 60,
    '1.1': 10,
}
const PageSpeedmate = () => {
    const [isShowSpeedmate, setisShowSpeedmate] = useRecoilState(isShowSpeedmateState);
    const [update, setUpdate] = useState(0);

    const sentensePauseHandler = () => {

        // let cnt = ZZAudioProgram.livePlayInfo.repeatCnt;
        sentenseCnt++;
        if (isYTMode) {
            if (sentenseCnt > 90) {
                speed = 1.1;
            } else if (sentenseCnt > 30) {
                speed = 1;
            } else if (sentenseCnt > 20) {
                speed = 0.9;
            } else if (sentenseCnt > 10) {
                speed = 0.8;
            }
        }

        ZZAudioProgram.livePlayInfo.speed = speed;
        // console.log('ZZAudioProgramcnt', sentenseCnt, ZZAudioProgram.livePlayInfo.speed);

        setUpdate(Math.random);

    }
    /// dddd-2
    useEffect(() => {

        ZZAudioProgram.play({
            idx: data.pageShadowingParam.sentenseIdx,
            totalIdx: data.pageShadowingParam.totalIdx,
            src: data.pageShadowingParam.audioSrc,
            speed: 0.7,
            repeat: 9999,
            repeatCnt: 9999,
            allplay: false,
        });
        // alert(data.pageShadowingParam.audioSrc + data.pageShadowingParam.sentenseIdx);
        // ZZAudioProgram.play({
        //     idx: data.pageShadowingParam.sentenseIdx,
        //     totalIdx: data.pageShadowingParam.totalIdx,
        //     src: data.pageShadowingParam.audioSrc,
        //     repeat: 9999,
        //     repeatCnt: 9999,
        //     allplay: false,
        // });
        sentenseCnt = 0;
        speed = 0.7;
        EventEmitter.subscribe(Event.SENTENSE_PAUSE, sentensePauseHandler);
        return () => {
            EventEmitter.remove(Event.SENTENSE_PAUSE, sentensePauseHandler);
        }
    }, [])
    /// dddd-3
    return (
        <>
            {
                isShowSpeedmate && <div className="PageSpeedmate">
                    <button onClick={() => {
                        ZZAudioProgram.pause();
                        ZZAudioProgram.livePlayInfo.speed = 1;

                        setisShowSpeedmate(false);
                    }} className="btn btn-close">
                        <i className="fas fa-times"></i>
                    </button>
                    <h2>  </h2>
                    <div className="box-sentense">
                        <div className="box-sentense-in">
                            <div className="en">{data.pageShadowingParam.sentenseEn}</div>
                            <div className="ko">{data.pageShadowingParam.sentenseKo}</div>
                        </div>
                        <div className="box-state">
                            <div className="dp-state"> : x{speed}</div>
                            <div className="dp-state">  : {sentenseCnt + 1}</div>
                        </div>

                    </div>


                    {isYTMode && <StepPill />}
                    {!isYTMode && <>
                        <div className="box-btn-speed">
                            <button className="btn btn-speed slow" onClick={() => {
                                speed = Number((speed - 0.1).toFixed(1));
                                ZZAudioProgram.livePlayInfo.speed = speed;
                                setUpdate(Math.random);
                            }}> <br></br></button>
                            <button className="btn btn-speed fast" onClick={() => {
                                speed = Number((speed + 0.1).toFixed(1));
                                ZZAudioProgram.livePlayInfo.speed = speed;
                                setUpdate(Math.random);
                            }}> <br></br></button>
                        </div>
                    </>}
                </div>
            }
        </>
    );
};
/// dddd-4
const getStateClass = (inSpeed: number, inSetSpeed: number) => {
    if (inSpeed === inSetSpeed) {
        return 'active';
    } else if (inSpeed > inSetSpeed) {
        return 'done';
    }
    return 'need';// done, active,
}
const getStateCnt = (inSetSpeed: number) => {
    if (speed < inSetSpeed) {
        return cntSetObj[String(inSetSpeed)];
    }
    const cnt = sentenseCnt;
    if (inSetSpeed === 0.7) {
        if (cnt < 10) {
            return 10 - cnt;
        } else {
            return 'done';
        }
    } else if (inSetSpeed === 0.8) {
        if (cnt < 20) {
            return 20 - cnt;
        } else {
            return 'done';
        }
    } else if (inSetSpeed === 0.9) {
        if (cnt < 30) {
            return 30 - cnt;
        } else {
            return 'done';
        }
    } else if (inSetSpeed === 1.0) {
        if (cnt < 90) {
            return 90 - cnt;
        } else {
            return 'done';
        }
    } else if (inSetSpeed === 1.1) {
        if (cnt < 100) {
            return 100 - cnt;
        } else {
            return 'done';
        }
    }
    return 'done';
}
/// dddd-5
const cntDisplay = (inStepSpeed: number) => {
    const getState = getStateCnt(inStepSpeed);
    if (getState === 'done') {
        return '';
    }
    return getState + '/' + cntSetObj[String(inStepSpeed)] + ' ';
}
const StepPill = () => {
    console.log('renStepPill');
    return (<div className="pill-scroll">
        <ul className="pills">
            <li className={classnames(["pill-item", getStateClass(speed, 0.7)])}>
                <div>x0.7 </div>
                <div>{cntDisplay(0.7)}</div>
            </li>
            <li className={classnames(["pill-arrow", getStateClass(speed, 0.7)])}>
                <i className="fas fa-arrow-right"></i>
            </li>
            <li className={classnames(["pill-item", getStateClass(speed, 0.8)])}>
                <div>x0.8 </div>
                <div>{cntDisplay(0.8)}</div>
            </li>
            <li className={classnames(["pill-arrow", getStateClass(speed, 0.8)])}>
                <i className="fas fa-arrow-right"></i>
            </li>
            <li className={classnames(["pill-item", getStateClass(speed, 0.9)])}>
                <div>x0.9 </div>
                <div>{cntDisplay(0.9)}</div>
            </li>
            <li className={classnames(["pill-arrow", getStateClass(speed, 0.9)])}>
                <i className="fas fa-arrow-right"></i>
            </li>
            <li className={classnames(["pill-item", getStateClass(speed, 1.0)])}>
                <div>x1 </div>
                <div>{cntDisplay(1.0)}</div>
            </li>
            <li className={classnames(["pill-arrow", getStateClass(speed, 1.0)])}>
                <i className="fas fa-arrow-right"></i>
            </li>
            <li className={classnames(["pill-item", getStateClass(speed, 1.1)])}>
                <div>x1.1 </div>
                <div>{cntDisplay(1.1)}</div>
            </li>
        </ul>
    </div>);
}

export default PageSpeedmate;