import qs from 'qs';
import React, { useEffect, useState } from 'react';
import data from '../core/data';
const printJSON = require('../resource/mainJSONPrint.json');
const CoursePrint = () => {
    let param: any = null;
    if (data.isHashURL) {
        param = qs.parse(window.location.hash.split('?')[1]);
    } else {
        param = qs.parse(window.location.search.substr(1));
    }
    console.log('paramparam', param)
    // const param = qs.parse(window.location.search.substr(1));
    const selectCourseJSON = printJSON.find((item: any, i: number) => {

        if (item.info.openID === param.id) {
            return item;
        }

    });
    let tr = [];
    if (selectCourseJSON) {
        tr = selectCourseJSON.list.map((item: any, i: number) => {
            return <DaySentenses arr={item} idx={i} key={i} />
        })
    }
    useEffect(() => {

    }, [])

    return (
        <div className="CoursePrint">
            {
                selectCourseJSON && <div>
                    <div className="header">
                        <div className="row-right">
                            <button className="btn btn-border" onClick={() => {

                                (window as any).printScript('-');
                            }}><i className="fas fa-file-download"></i>  </button>
                        </div>

                    </div>
                    <table id="script-print-table">
                        <thead>
                            <tr >
                                <td ></td>
                                <td >No.</td>
                                <td ></td>
                                <td ></td>
                                <td ></td>
                            </tr>
                        </thead>
                        <tbody>
                            {tr}

                        </tbody>
                    </table>


                </div>
            }
        </div>
    );
};
const DaySentenses = ({
    arr,
    idx,
}: {
    arr: any,
    idx: number,
}) => {
    const SentenseList = arr.map((item: any, i: number) => {
        return <tr key={'tr' + i} className={`tr-day n-${i}`} >
            {i === 0 && <td key={'tr' + i} rowSpan={arr.length}>DAY{idx + 1}</td>}
            <td data-tableexport-xlsxformatid="1">{i + 1}</td>
            <td className="td-text-left">{item.ko}</td>
            <td className="td-text-left">{item.en}</td>
            <td className="td-memo">&nbsp;</td>
        </tr>
    })
    return (<>
        {SentenseList}
    </>);
}


export default CoursePrint;