import classnames from 'classnames';
import dayjs from 'dayjs';
import { css } from 'emotion';
import React, { useState } from 'react';
import { atom, useRecoilState, useRecoilValue } from 'recoil';

const sentenseCntHistory: any = {
    "2020": [
        [2, 0, 100,],
        null,
        [],
    ],
    "2021": [
        [4, 0, 120, null, 10, 0, 0, 0, 3, 2, 3],
        null,
        [],
    ]
}

const updateCntHistory = (inDay: string, inPlusCnt: number) => {
    // year
    let yearArr = sentenseCntHistory[inDay.substr(0, 4)];
    if (!yearArr) yearArr = sentenseCntHistory[inDay.substr(0, 4)] = [];
    // month
    let monthArr = yearArr[parseInt(inDay.substr(4, 2)) - 1];
    // console.log('tempa-- ',monthArr)
    if (!monthArr) monthArr = yearArr[parseInt(inDay.substr(4, 2)) - 1] = [];
    // day
    let dayNum = monthArr[parseInt(inDay.substr(6, 2)) - 1];
    if (!dayNum) dayNum = 0;
    monthArr[parseInt(inDay.substr(6, 2)) - 1] = dayNum + inPlusCnt;

}
const goalCnt: number = 20;
const currentDateState = atom({
    key: "currentDateState",
    default: dayjs.utc().local(),
})

const PageRecord = () => {
    const [update, setUpdate] = useState(0);
    return (
        <div className="PageRecord">
            <div className="calendar">
                <div>
                    <CalNav />
                </div>
                <div className="cal-table">
                    <WeekHeader />
                    <WeekDays />
                </div>

                {/* <div>
                    guide   
                       
                </div> */}

                <button onClick={() => {
                    updateCntHistory("20210127", 1);
                    setUpdate(Math.random());

                }}>+1</button>
            </div>
        </div>
    );
};
const CalNav = () => {
    const [currentDate, setCurrentDate] = useRecoilState(currentDateState);
    let todayStr = currentDate.format('YYYY.MM');
    return (
        <div className="CalNav">
            <div className="child l">
                <button className="btn" onClick={() => {
                    setCurrentDate(currentDate.add(-1, "month"));
                }}><i className="fas fa-arrow-alt-circle-left"></i></button>
            </div>
            <div className="child c">{todayStr}</div>
            <div className="child r">
                <button className="btn" onClick={() => {
                    setCurrentDate(currentDate.add(1, "month"));
                }}><i className="fas fa-arrow-alt-circle-right"></i></button>
            </div>
        </div>
    )
}

const WeekHeader = () => {

    return (
        <>
            {/* temp <div>
                 :   100   !
                <div></div>
            </div> */}

            <ul className="tr-week th-week">
                <li className="td-day">S</li>
                <li className="td-day">M</li>
                <li className="td-day">T</li>
                <li className="td-day">W</li>
                <li className="td-day">T</li>
                <li className="td-day">F</li>
                <li className="td-day">S</li>
            </ul>
        </>

    );
}
type DayDisplayInfo = {
    date: Date,
    repeatPercent: number,
    repeatCnt: number,//   
}

const Day = ({ info, classarr }: { info: DayDisplayInfo, classarr: string[] }) => {
    let dateStr = info.date.getDate();

    if (info.repeatPercent > 100) info.repeatPercent = 100;
    return (<li className={classnames(classarr.concat(["td-day"]))}>
        <div>{dateStr}</div>
        <div className="graph-border">
            <div className="cnt">{info.repeatCnt > 0 ? info.repeatCnt : ''}</div>
            <div className={classnames("graph-bar", css`
                width: ${info.repeatPercent}%;
            `)}></div>
        </div>
    </li>);
}

const dateBeforeDays = (date: Date, days: number) => {
    return new Date((date as Date).getTime() - 60 * 60 * 24 * 1000 * days);
}
const getDayDisplayInfo = (inDayInfo: Date): DayDisplayInfo => {
    // sentenseCntHistory
    const nyear = inDayInfo.getFullYear();
    const nmonth = inDayInfo.getMonth();
    const nday = inDayInfo.getDate();
    let repeatCnt = 0;
    try {
        repeatCnt = sentenseCntHistory[nyear][nmonth][nday - 1];
    } catch (e) {

    }
    if (!repeatCnt) repeatCnt = 0;
    return {
        date: inDayInfo,
        repeatCnt: repeatCnt,
        repeatPercent: (repeatCnt / goalCnt) * 100,
    }

}
const WeekDays = () => {
    const currentDate = useRecoilValue(currentDateState);
    const Days: any = [];
    const showDate = new Date(currentDate.format('YYYY-MM-DD'));
    const firstDate = new Date(showDate.getFullYear(), showDate.getMonth(), 1); // 
    const lastDate = new Date(
        showDate.getFullYear(),
        showDate.getMonth() + 1,
        0
    ).getDate(); // 
    let firstWeek = firstDate.getDay();

    for (let i = 1, max = 7 * 6; i <= max; i++) {
        let dateData: Date;
        let dayNum: number;
        let classArr: Array<string> = [];

        // ?   Date  , ,   class 
        if (i <= firstWeek) {
            //  -  
            dayNum = dateBeforeDays(firstDate, Math.abs(i - firstWeek - 1)).getDate(); //  -  - 1;
            dateData = dateBeforeDays(firstDate, Math.abs(i - firstWeek - 1));
            classArr.push('before');
        } else if (i > firstWeek + lastDate) {
            // 
            dayNum = i - firstWeek - lastDate; //  -  - ;
            dateData = new Date(showDate.getFullYear(), showDate.getMonth() + 1, dayNum);
            classArr.push('after');
        } else {
            // 
            dayNum = i - firstWeek; //  - ;
            dateData = new Date(showDate.getFullYear(), showDate.getMonth(), dayNum); // 
        }



        Days.push(
            <Day info={getDayDisplayInfo(dateData)}
                classarr={classArr}
                key={String(dayNum)}
            />
        );
    }
    return (<div>
        <ul className="tr-week">{Days.slice(0, 7)}</ul>
        <ul className="tr-week">{Days.slice(7, 14)}</ul>
        <ul className="tr-week">{Days.slice(14, 21)}</ul>
        <ul className="tr-week">{Days.slice(21, 28)}</ul>
        <ul className="tr-week">{Days.slice(28, 35)}</ul>
        <ul className="tr-week">{Days.slice(35, 42)}</ul>
    </div>);
}



export default PageRecord;