import classnames from 'classnames';
import React, { useState } from 'react';
import WCourseItem from './WCourseItem';
const WCourse = ({
    json,
    parentName,
}: {
    json: any
    parentName: string,
}) => {
    let viewType = 'normal';
    const courseID = json.courseID;
    const topics = json.item.map((item2: any, i2: number) => {
        if (json.itemViewType) {
            viewType = json.itemViewType;
        }
        return <WCourseItem
            viewType={viewType}
            itemJSON={item2}
            courseID={courseID}
            idx={i2}
            key={courseID + i2}
        />

    })
    const isShowTitle = parentName === 'CourseMain' ? false : true;
    const [isHide, setIsHide] = useState(parentName === 'CourseMain' ? false : true);
    return (
        <div className="Course">
            {
                //
                isShowTitle && <h3 className="title" onClick={() => {
                    setIsHide(!isHide);
                }}>
                    <Tags str={json.tags} key={courseID} />
                    {
                        isHide ? <i className="fas fa-plus i-icon"></i> : <i className="fas fa-minus i-icon"></i>
                    }
                    {json.title}
                    <span className="badge">{json.item.length}</span>
                </h3>
            }
            <ul className={classnames(['slideup', { "hide": isHide }])}>
                {!isHide && topics}
            </ul>

        </div>
    );
};


const Tags = ({
    str
}: {
    str: string
}) => {
    const arr = str.split(',');
    let Tag = arr.map((item: string, i: number) => {
        return <span className="tag" key={i}>#{item}</span>
    });
    if (str === '') {
        Tag = [];
    }
    return (
        <div className="Tags">
            {Tag}
        </div>
    );
}

export default WCourse;