import React, { useEffect, useState } from 'react';
import EventEmitter, { Event } from '../core/EventEmitter';
import classnames from 'classnames';
import { css } from 'emotion';

let interval: any = null;
const ToastAlert = () => {
    const [info, setInfo] = useState({
        message: '',
        mtime: 0,
    });
    useEffect(() => {
        EventEmitter.subscribe(Event.SHOW_TOAST_MESSAGE, (option: {
            message: string,
            mtime: number
        }) => {
            setInfo(option);
            if (option.mtime === 0) {
                option.mtime = 3000;
            }
            if (interval) {
                clearInterval(interval);
            }
            interval = setInterval(() => {
                clearInterval(interval);
                setInfo({
                    message: '',
                    mtime: 0,
                })
            }, option.mtime)
        });
    }, [])
    return (
        <div className={classnames("ToastAlert", css`
            bottom:${info.message === '' ? '-100px' : '20px'}
        `)}>
            {info.message}
        </div>
    );
};

export default ToastAlert;