import React, { useEffect, useState } from 'react';
import AlertBasic from '../components/AlertBasic';
import EventEmitter, { Event } from '../core/EventEmitter';
// import AlertNoticeInapp from '../components/AlertNoticeInapp';
import AlertBuyProduct from '../components/AlertBuyProduct';
import { useRecoilState, useSetRecoilState } from 'recoil';
// import { sentenseRepeatIdxState } from '../core/store';
import data from '../core/data';
import AlertBuyProduct2 from '../components/AlertBuyProduct2';

let openParam: any = null;
const Alert = () => {
    const [moduleName, setModuleName] = useState('');
    // const setIsBodyNoScroll = useSetRecoilState(isBodyNoScrollState);

    useEffect(() => {
        EventEmitter.subscribe(Event.SHOW_ALERT, (options: any) => {
            openParam = options;
            setModuleName(options.moduleName);
            data.bodyOverflowHidden(true);
            // setIsBodyNoScroll(true);
            if (options.moduleName === 'AlertNoticeInapp') {
                // setUpdatePagePlaylist(updatePagePlaylist + 1);
                // setSentenseRepeatIdxState(9999);
                // data.setCurrPlayBtnState('stop');
            }

        });
        EventEmitter.subscribe(Event.HIDE_ALERT, (options: any) => {
            setModuleName('');
            data.bodyOverflowHidden(false);
            // setIsBodyNoScroll(false);
        });
    }, [])

    const AlertModule = (() => {

        switch (moduleName) {
            case 'AlertBasic':
                return <AlertBasic key="AlertBasic" />;
                break;

            case 'AlertBuyProduct':
                return <AlertBuyProduct key="AlertBuyProduct" openParam={openParam} />;
                break;

            case 'AlertBuyProduct2':
                return <AlertBuyProduct2 key="AlertBuyProduct2" openParam={openParam} />;
                break;
        }
    })();
    return (
        <>
            {
                moduleName !== '' && <div className="view-Alert">
                    {AlertModule}
                </div>
            }
        </>
    );
};

export default Alert;


