import classnames from 'classnames';
import React, { useEffect, useState } from 'react';
import { useSetRecoilState } from 'recoil';
import ZZAudioProgram from '../components/ZZAudioProgram';
// import appBridge from './core/AppBridge';
import data from '../core/data';
// import lang from "./core/i18n";
import { isShowShadowingState } from '../core/store';
import Equalizer from './Equalizer';
import MDictation from './MDictation';

const selfName = 'PageShadowing';
let temprepeatMode: any;
const PageShadowing = () => {
    const setIsShowShadowing = useSetRecoilState(isShowShadowingState);
    useEffect(() => {

        ZZAudioProgram.play({
            idx: data.pageShadowingParam.sentenseIdx,
            totalIdx: data.pageShadowingParam.totalIdx,
            src: data.pageShadowingParam.audioSrc,
            repeat: 9999,
            repeatCnt: 9999,
            allplay: false,
        });

        // temprepeatMode = appBridge.settingInfo.repeatMode;
        // appBridge.settingInfo.repeatMode = '1';
        // data.setCurrPlayBtnState('play');
        // data.audioPlayPause(data.pageShadowingParam.sentenseIdx);

        //     
        (window as any).scrollTo(0, 10);
        setTimeout(() => {
            (window as any).scrollTo(0, 0);
        }, 1000)

        return () => {
            // data.setCurrPlayBtnState('stop');
        }
    }, []);
    return (
        <div className="PageShadowing">
            {/* <PageHeader parentSelf={selfName} /> */}
            {/* <h2>Shadowing & Dictation</h2> */}
            <button onClick={() => {
                ZZAudioProgram.pause();
                setIsShowShadowing(false);
            }} className="btn btn-close">
                <i className="fas fa-times"></i>
            </button>
            <MDictation
                sentenseEn={data.pageShadowingParam.sentenseEn}
                sentenseKo={data.pageShadowingParam.sentenseKo}
            />
            <ControlBtns />
        </div>
    );
};

