    parseQuery() {
        // const queryDivideStr = 'AGQpG10Xdk&';
        console.log('(window as any).location', (window as any).location);
        const isHashMode = true;
        let d = '';
        if (isHashMode) {
            const hash = (window as any).location.hash;
            d = hash.split('?d=')[1];
        } else {
            d = (window as any).location.search.substring(3);
        }

        const incryptString = d.split(queryDivideStr)[0];
        const etcparam = qs.parse(d.split(queryDivideStr)[1]);
        const decryptParam = this.decryptURL(incryptString);
        return Object.assign({}, decryptParam, etcparam);
    }

    // setParameter => gotoPage
    gotoPage(inParam: string, inQuery: string) {

        if (inParam === 'home' || inParam === '/') {
            window.location.href = '/';
            return;
        }
        // alert('TODO:gotoPage');
        if (inQuery) inQuery = `?d=${inQuery}`;
        window.location.href = `#/${inParam}${inQuery}`;
    }
