    // / 
    gotoNextPrev(idx: number, cid: string, dir: 'next' | 'prev') {
        const resultItem = mainJSON.find((item: any, i: number) => {
            return item.courseID === cid;
        })
        if (idx === 0 && dir === 'prev') {
            alert('  .(  )');
            return;
        }
        if (idx === resultItem.item.length && dir === 'next') {
            alert('  .');
            return;
        }
        const overIdx = dir === 'next' ? 1 : -1;
        const nextItem = resultItem.item[idx + overIdx];
        if (!nextItem) {
            alert((dir === 'next' ? '' : '') + "  .");
            return;
        }

        this.gotoPage('CoursePlay', resultItem.item[idx + overIdx].query);
    }
    setClipHistory(tpid: string) {

        if (!fbase.userMoreInfo.historyClips) {
            fbase.userMoreInfo.historyClips = [];
        }

        const arr = fbase.userMoreInfo.historyClips.filter((item: string) => {
            //     
            return item !== tpid;
        })

        arr.push(tpid);

        fbase.userMoreInfo.historyClips = arr;
        // debugger;
        // appBridge.syncStorageData();
        fbase.syncMoreInfo();
    }
    //     
    convertSubscriptionInfoStrToObj(str: string) {
        const obj = {
            termCD: '',
            st: '',
            et: '',
            stInt: 0,
            etInt: 0

        }
        if (str) {
            const arr = str.split('##');
            if (arr.length === 3) {
                obj.termCD = arr[0];
                obj.st = arr[1];
                obj.et = arr[2];
                obj.stInt = parseInt(arr[1]);
                obj.etInt = parseInt(arr[2]);
            }
        }
        return obj;
    }
    setSettingKeepClipSentense(
        type1: 'clip' | 'sentense' | 'done',
        type2: 'add' | 'remove',
        value: any) {
        console.log('setSettingKeepClipSentense', type1, type2, value);
        let arr: string[] = [];
        let newArr: string[] = [];
        if (type1 === 'clip') {

            arr = fbase.userMoreInfo.keepClips;
        } else if (type1 === 'sentense') {
            arr = fbase.userMoreInfo.keepSentense;
        } else if (type1 === 'done') {
            arr = fbase.userMoreInfo.doneSentense;
        }
        if (arr.includes(value)) {
            //    
            if (type2 === 'remove') {
                //  
                newArr = arr.filter((item, i) => {
                    return item !== value;
                });
            }

        } else {
            //    
            if (type2 === 'add') {
                //  
                arr.push(value)
                newArr = JSON.parse(JSON.stringify(arr));
            }

        }

        if (type1 === 'clip') {
            fbase.userMoreInfo.keepClips = newArr;

        } else if (type1 === 'sentense') {
            fbase.userMoreInfo.keepSentense = newArr;

        } else if (type1 === 'done') {
            fbase.userMoreInfo.doneSentense = newArr;

        }

        fbase.syncMoreInfo();
    }
    decryptURL(text: string) {
        try {
            text = text.substring(7);
            const bytes = CryptoJS.AES.decrypt(text, key);
            let parse = null;
            const make1 = bytes.toString(CryptoJS.enc.Utf8);
            parse = JSON.parse(make1);
            return parse;
        } catch (err) {
            // console.error(err);
            return;
        }
    }
    bodyOverflowHidden(tf: boolean) {
        if (tf) {
            document.body.classList.add('noscroll');
        } else {
            document.body.classList.remove('noscroll');
        }

    }
    numberWithCommas = (x: number) => {
        let parts = x.toString().split(".");
        parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        return parts.join(".");
    }
