    //  empty:  on: expired01:
    getSubscriptionState(infoStr: string) {
        console.log('tmplog_getSubscriptionState', infoStr)

        let subscriptionType = 'empty';
        if (['needLogin', 'needPurchase'].includes(infoStr)) {
            subscriptionType = infoStr;
        } else {
            let todayInt = parseInt(Days.utc().local().format('YYYYMMDD'));
            const infoArr = infoStr.split('##');

            let [productCD, stStr, edStr] = infoArr;
            if (infoArr.length === 3) {
                if (productCD.substr(0, 1) === 't') {
                    //   
                    productCD = productCD.substr(1);
                }
                // const productCDtoStr: string = productTable[productCD].title;
                subscriptionType = 'on'

                if (todayInt > parseInt(edStr)) {
                    //  
                    subscriptionType = 'expired01';

                }
            } else {
                subscriptionType = 'empty';

            }

        }
        return subscriptionType;
    }
