import React, { useEffect, useState } from 'react';
import data from '../core/data';
import EventEmitter, { Event } from '../core/EventEmitter';
import fbase, { authService } from '../fb/fbase';

const Mypage = () => {
    const [isCheckedAuth, setisCheckedAuth] = useState(false);
    const checkLoginUser = () => {
        if (!fbase.userInfo) {
            alert('    .')
            //  
            data.gotoPage('home', '');
        }
    }
    useEffect(() => {
        //  userInfo    
        // data.setClipHistory(tpid);
        // firebase    topic history Sync
        EventEmitter.subscribe(Event.ON_AUTH_STATE_CHANGED, () => {
            setisCheckedAuth(true);
            checkLoginUser();

        });
        if (fbase.userInfo !== 'loading') {
            setisCheckedAuth(true);
            checkLoginUser();
        }

    }, []);
    const currSubsArr: string[] = fbase.currentAvailableSubscriptionInfo();
    const [inputCode, setInputCode] = useState('');
    const onChangeInput = (e: any) => {
        setInputCode(e.target.value);
    }

    return (
        <div className="Mypage">

            <div className="group-user">
                <div className="user">{fbase.userInfo && fbase.userInfo.email && fbase.userInfo.email.split('@')[0]}</div>
                <div><button className="btn btn-border round" onClick={() => {
                    authService.signOut().then(() => {
                        (window as any).location.href = "/";
                    });
                }}></button></div>
            </div>
            <div className="divide-line"></div>
            <div className="group">
                <div className="title"></div>
                <div className="content content-sub">
                    {isCheckedAuth && <SubscriptionHistory arr={currSubsArr} />}
                </div>
            </div>
            <div className="divide-line"></div>
            <div className="group">
                <div className="title"> </div>
                <div className="content content-buy">
                    <a className="btn btn-border btn-smartstore" href="https://smartstore.naver.com/casete" target="_blank"> </a>
                </div>
            </div>
            <div className="divide-line"></div>
            <div className="group">
                <div className="title"> </div>
                <div className="content content-input">
                    <div className="input-code">
                        <input value={inputCode} onChange={onChangeInput} placeholder="  "></input>
                    </div>
                    <div className="input-btn">
                        <button className="btn btn-border" onClick={() => {
                            fbase.submitCouponCode(inputCode, 'mypage');

                        }}></button>
                    </div>
                </div>
            </div>
        </div>
    );
};

const SubscriptionHistory = ({ arr }: { arr: string[] }) => {
    const Comp = arr.map((item: string, i: number) => {
        const arr = item.split('##');
        return <li className="terms">{
            `${data.dateMyFormat(arr[1])} ~ ${data.dateMyFormat(arr[2])}`
        }</li>
    })
    return (
        <>
            {
                arr.length > 0
                    ?
                    <div>
                        <div className="freepassuser"><i className="far fa-laugh"></i></div>
                        <ul className="list">
                            {Comp}
                        </ul>
                    </div>

                    :
                    <div><i className="far fa-laugh"></i>  </div>
            }

        </>);
}

export default Mypage;