import classnames from 'classnames';
import { css } from 'emotion';
import React, { useState } from 'react';
import data from '../core/data';
import fbase from '../fb/fbase';
import {
    BrowserView, isBrowser,
    MobileView
} from "react-device-detect";
import EventEmitter, { Event } from '../core/EventEmitter';
import { useRecoilState, useRecoilValue } from 'recoil';
import { lastURLState } from '../core/store';

let uniqCnt = 0;
const PageHome = () => {
    const [lastURL, setlastURL] = useRecoilState(lastURLState);
    // data.topicMainJSON = topicMainJSON;
    const course1 = data.mainJSON.map((item1: any, i: number) => {
        if (item1.isHomeList === "true") {
            return <CourseItem json={item1} key={uniqCnt++} />
        }

    });

    EventEmitter.subscribe(Event.ON_AUTH_STATE_CHANGED, () => {
        // if (fbase.userMoreInfo.historyClips) {
        //     const arr = fbase.userMoreInfo.historyClips.slice(0);
        //     setListeningTpid(arr.reverse()[0]);
        // }
        setlastURL(fbase.userMoreInfo.lastURL);
    });
    const [inputEmail, setInputEmail] = useState('');
    const onChange = (event: any) => {
        const {
            target: { name, value },
        } = event;
        if (name === "email") {
            setInputEmail(value);
        }
    }
    const subscriptionInfo = fbase.isSubscriptionInfo();
    const isOwner = data.getSubscriptionState(fbase.isSubscriptionInfo()) === 'on';
    // console.log("subscriptionInfosubscriptionInfo", subscriptionInfo, isOwner)
    return (
        <div className="PageHome">
            <div className="notice">
                <BrowserView>
                    <div className="tit-main">
                        <div className="pc-br">       </div>
                        <div className="pc-br"><span className="em"> </span></div>
                    </div>
                    <div className="tit-sub">
                        <div className="pc-br">       </div>
                        {/* <div className="pc-br">       .</div> */}
                    </div>
                </BrowserView>
                <MobileView>
                    <div className="tit-main">
                          <br></br>    <br></br><span className="em"> </span>
                    </div>
                    <div className="tit-sub">
                            <br></br>    , ,   .
                    </div>
                </MobileView>

                {
                    subscriptionInfo === 'needLogin' && <div className="group-user-email">
                        <div className="free-message"><i className="far fa-comment-dots"></i><div className="pill pill-event">EVENT</div>   .</div>
                        <div>
                            <input className="form-styling"
                                placeholder="  "
                                autoCapitalize="false"
                                name="email" type="text" onChange={onChange} value={inputEmail}></input>
                        </div>
                        <button onClick={() => {
                            EventEmitter.dispatch(Event.FB_START_LOGIN, {
                                from: "joinFromHome",
                                email: inputEmail,
                            });
                        }} className="btn btn-start-free">  <i className="fas fa-long-arrow-alt-right"></i></button>
                        <div className="have-id">   <button onClick={() => {
                            EventEmitter.dispatch(Event.FB_START_LOGIN, {
                                from: "loginFromHome",
                                email: inputEmail,
                            });
                        }} className="btn btn-login"></button></div>
                    </div>
                }
                {
                    //    (!isOwnew - )
                    // (subscriptionInfo && !isOwner) && <div>
                    //     <button onClick={() => {

                    //     }} className="btn btn-start-free">  <i className="fas fa-long-arrow-alt-right"></i></button>
                    // </div>
                }


            </div>
            {
                lastURL && <div className="last-listening">
                    <button onClick={() => {
                        data.gotoPage('CoursePlay', lastURL);
                    }} className="btn btn-listening"> <i className="fas fa-caret-square-right"></i></button>
                </div>
            }

            <ul className="course-list">
                {course1}
            </ul>
        </div>
    );
};

const CourseItem = ({
    json
}: {
    json: any
}) => {
    const isOwner = !['needLogin', 'needPurchase'].includes(fbase.isSubscriptionInfo());
    // const isOwner: boolean = true;

    return (
        <>

            <li className="course-item" onClick={() => {
                data.gotoPage('CourseMain', json.query);
            }}>
                <div className={classnames(["cover-img", css`
                background-image: url("${data.getRootPath()}resource/courses/${json.courseID}/cover.png");
            `])}>
                    <div className={classnames(["tag-level", `level${json.level}`])}>
                        <div className="title">LEVEL</div>
                        <div className="guage g1"></div>
                        <div className="guage g2"></div>
                        <div className="guage g3"></div>
                        <div className="guage g4"></div>
                    </div>
                    {/* <div>Lv.1</div> */}
                    <div className={classnames(["tag-iscomplete", { ing: json.isMakeComplete === "false" }])}>
                        {json.isMakeComplete === "false" ? "" : ""}
                    </div>
                </div>
                <div className="info">
                    <h3>{json.title}</h3>
                    <div className="info-sub">
                        <div className="course-num">{json.item.length} </div>
                        <Tags str={json.tags} key={json.courseID} />
                        {/* <div className="btns-func">
                            {data.numberWithCommas(parseInt(json.prices[0]))}
                            {
                                isOwner && <span></span>
                            }
                        </div> */}

                    </div>


                </div>
            </li>
        </>

    )
}

export const Tags = ({
    str
}: {
    str: string
}) => {
    const arr = str.split(',');
    let Tag = arr.map((item: string, i: number) => {
        return <span className="tag" key={i}>#{item}</span>
    });
    if (str === '') {
        Tag = [];
    }
    return (
        <div className="Tags">
            {Tag}
        </div>
    );
}

export default PageHome;