    syncMoreInfo() {
        console.log('docs.data()-sync');
        //   
        if (!this.userInfo || this.userInfo === '' || this.userInfo === 'loading') {
            // alert(this.userInfo);
            return;
        }
        //    
        if (!this.initUserInfoMerge) return;

        dbService.collection("userstatus").doc(this.makeDocNameWithUserInfo()).set(this.userMoreInfo, { merge: true }).then(function () {
            console.log("Document successfully written!");
        });
    }
    makeDocNameWithUserInfo() {
        let id = this.userInfo.email.split('@')[0];
        const reg = /[\{\}\[\]\/?.,;:|\)*~`!^\-_+<>@\#$%&\\\=\(\'\"]/gi;
        if (reg.test(id)) {
            id = id.replace(reg, '');
        }
        id += 'mXCeNH6A5fjdrDfaOjDe';//     
        id = id.substr(0, 10);// 10 
        // console.log('this.userInfo.email', this.userInfo.email);
        return id + this.userInfo.uid;
    }
