

/**
 * use
 * import EventEmitter, {Event} from "common/events/EventEmitter";
 *
 * dispatch
 * EventEmitter.dispatch(Event.EVENT_EXAMPLE, {   });
 *
 * subscribe
 * EventEmitter.subscribe(Event.EVENT_EXAMPLE, (options:any)=>{
 *  {   }
 * });
 */
export class Event {

	public static ON_AUTH_STATE_CHANGED: string = "onAuthStateChanged";
	public static FB_START_LOGIN: string = "fbStartLogin";
	public static IS_APP_READY: string = "isAppReady";
	public static INSERT_PAGE: string = "insertPage";
	public static REPLACE_PAGE: string = "replacePage";
	public static GOTO_BACK: string = "gotoBack";
	public static RESET_PAGE: string = "resetPage";
	public static SHOW_ALERT: string = "showAlert";
	public static HIDE_ALERT: string = "hideAlert";
	public static INIT_AUDIO: string = 'initAudio';
	public static AUDIO_PLAY_PAUSE: string = 'audioPlayPause';
	public static AUDIO_STOP: string = 'audioStop';
	public static SENTENSE_PLAYING: string = 'sentensePlaying';
	public static SENTENSE_PAUSE: string = 'sentensePause';
	public static CONTROL_UI_UPDATE: string = 'controlUIUpdate';
	public static SET_BTN_PLAY_STATE: string = 'setBtnPlayState';//     
	public static CHANGE_BTN_PLAY_STATE: string = "changeBtnPlayState";//      
	public static SHOW_NOTI_MESSAGE: string = "showNotiMessage";
	public static PURCHASE_COMPLETE: string = "purchaseComplete";
	public static FREETIME_UPDATED: string = "freetimeUpdated";
	public static CLICK_HOTCLIP_THUMB: string = "clickHotclipThumb";
	public static SHOW_LOADING: string = "showLoading";
	public static HIDE_LOADING: string = "hideLoading";
	public static AUDIO_PLAY_COUNT: string = "audioPlayClunt";
	public static CHANGE_TPID: string = "changeTPID";
	public static SHOW_TOAST_MESSAGE: string = "showToastMessage";


	public events: any = {};
	public dispatch(event: string, data?: any): void {
		if (!this.events[event]) return;
		this.events[event].forEach((callback: (arg0: any) => void) =>
			callback(data)
		);
	}
	public subscribe(event: string, callback: any): void {
		if (!this.events[event]) this.events[event] = [];
		this.events[event].push(callback);
	}
	public remove(event: string, callback?: any): void {
		// if (this.events[event]) this.events[event] = null;
		if (this.events[event]) this.events[event].splice(this.events[event].indexOf(callback), 1);
	}
}

export default new Event();
