
import React, { useEffect, useState } from 'react';
import EventEmitter, { Event } from '../core/EventEmitter';
import fbase from './fbase';
import FBViewLogin from './FBViewLogin';
import FBViewLogout from './FBViewLogout';
// import "./FBState.scss";

const FBState = () => {

    const [checkedLogin, setCheckedLogin] = useState(false);
    const [update, setUpdate] = useState(0);
    useEffect(() => {
        EventEmitter.subscribe(Event.ON_AUTH_STATE_CHANGED, (userInfo: any) => {
            console.log('Event.ON_AUTH_STATE_CHANGED')
            setCheckedLogin(true);
            setUpdate(Math.random());
        });
        fbase.initLogin();

    }, []);

    return (
        <div className="FBState">
            {
                checkedLogin
                    ?
                    fbase.userInfo ? <FBViewLogin /> : <FBViewLogout />
                    :
                    <div>loading...</div>
            }
        </div>
    );
};

export default FBState;