
import React, { useState } from 'react';
import data, { productTable } from '../core/data';
import fbase, { authService, dbService } from './fbase';
import Days from '../core/Days';
import classnames from 'classnames';
import EventEmitter, { Event } from '../core/EventEmitter';
import axios from 'axios';


const FBViewLogin = () => {
    const [isShowLoginPopup, setisShowLoginPopup] = useState(false);
    const isSubscriptionInfo = fbase.isSubscriptionInfo();

    const isOwner = data.getSubscriptionState(fbase.isSubscriptionInfo()) === 'on';
    const [inputCode, setInputCode] = useState('');
    const onChangeInput = (e: any) => {

        setInputCode(e.target.value);
    }
    return (
        <div className="FBViewLogin">
            <div>
                <button onClick={() => {
                    // setisShowLoginPopup(true);
                    data.gotoPage('mypage', '');
                }} className={classnames(["btn btn-userinfo", { "login": isOwner }])}><i className="fas fa-user-circle">

                    </i>
                    <div className="print-user">
                        {/* <div className="user-id">maca222oshu</div> */}
                        <div className="user-id">{fbase.userInfo.email.split('@')[0]}</div>
                        <span className="purchase-state">({isOwner ? "" : "  "})</span>
                    </div>
                </button>
            </div>
            {
                isShowLoginPopup &&
                <div className="popup-login-info">
                    <div className="popup-login-info-in">
                        <div className="popup-header">
                            <h3 className="center">
                                
                            </h3>
                            <div className="right">
                                <button onClick={() => {
                                    setisShowLoginPopup(false);
                                }} className="btn btn-close"><i className="fas fa-times"></i></button>
                            </div>
                        </div>
                        <div className="area-user">
                            <span className="user-id">{fbase.userInfo.email.split('@')[0]}</span>
                            <button className="btn btn-border round" onClick={() => {
                                authService.signOut().then(() => {
                                    (window as any).location.href = "/";
                                });
                            }}></button>
                        </div>



                        <div className="purchase-list">
                            <h4 className="title"><i className="fas fa-book"></i> </h4>
                            {/* <SubscriptionInfo infoStr={fbase.isSubscriptionInfo()} /> */}
                            <SubscriptionInfoArr />
                            <div>
                                <h4 className="title"><i className="fas fa-book"></i> </h4>
                                <button onClick={() => {
                                    fbase.buyFbSubscriptionInfo('t1w');
                                }}> </button>
                            </div>
                            <div>
                                <h4 className="title"><i className="fas fa-book"></i> </h4>
                                <div className="group-input-code">
                                    <div className="input-code">
                                        <input value={inputCode} onChange={onChangeInput} placeholder="  "></input>
                                    </div>
                                    <div className="input-btn">
                                        <button onClick={() => {

                                            fbase.submitCouponCode(inputCode, 'firstjoin');

                                        }}></button>
                                    </div>
                                </div>

                            </div>
                        </div>


                        {/* <button onClick={() => {
                            // 
                            dbService.collection('userstatus').add({ todo: "test" }).then((res: any) => {
                                console.log(res)
                            })
                        }}></button> */}
                    </div>

                </div>
            }


        </div>
    );
};

const SubscriptionInfoArr = () => {
    return (<ul>SubscriptionInfoArr</ul>);
}

const SubscriptionInfo = ({
    infoStr,
}: {
    infoStr: string
}) => {
    // console.log('tmplog_getSubscriptionState', infoStr)
    let subscriptionType = data.getSubscriptionState(infoStr);
    let dp1 = '';
    let dp2 = '';

    if (subscriptionType === 'empty') {
        dp1 = '   ';
    } else {
        let todayInt = parseInt(Days.utc().local().format('YYYYMMDD'));
        const infoArr = infoStr.split('##');

        let [productCD, stStr, edStr] = infoArr;
        if (productCD.substr(0, 1) === 't') {
            productCD = productCD.substr(1);
        }
        let productCDtoStr: string = '';
        if (productCD === '1w') {
            productCDtoStr = '1'
        } else {
            productCDtoStr = productTable[productCD].title;
        }

        dp2 = `${data.dateMyFormat(stStr)} ~ ${data.dateMyFormat(edStr)}`
        if (subscriptionType === 'on') {
            dp1 = productCDtoStr + '   ';
        } else if (subscriptionType === 'expired01') {
            dp1 = ' ';
        }
    }

    return (
        <ul className={classnames(["list", subscriptionType])}>
            <li className="dp1">
                {dp1}
                {
                    ["empty"].includes(subscriptionType) &&
                    <button onClick={() => {
                        EventEmitter.dispatch(Event.SHOW_ALERT, {
                            moduleName: "AlertBuyProduct2",
                            courseID: '',
                        });
                    }} className="btn btn-border btn-buy">
                        <i className="fas fa-plus-square"></i> 
                    </button>
                }
                {
                    ["expired01"].includes(subscriptionType) &&
                    <button onClick={() => {
                        EventEmitter.dispatch(Event.SHOW_ALERT, {
                            moduleName: "AlertBuyProduct2",
                            courseID: '',
                        });
                    }} className="btn btn-border btn-buy">
                        <i className="fas fa-plus-square"></i> 
                    </button>
                }
            </li>
            {
                dp2 && <li className="dp2">{dp2}</li>
            }

        </ul>
    );
}

export default FBViewLogin;