import classnames from 'classnames';
import { shuffle as _shuffle } from 'lodash';
import React, { useState } from 'react';

type TWord = {
    word: string,
    oriIdx: number,
}
let sentense = "";
let sentenseSplit: string[] = [];
let correctArr: string[] = [];// 
let shuffleWords: TWord[] = [];

const MDictation = ({
    sentenseEn,
    sentenseKo,
}: {
    sentenseEn: string,
    sentenseKo: string,
}) => {
    //   
    sentenseEn = sentenseEn.trim()
    if (sentense !== sentenseEn) {
        sentense = sentenseEn;
        sentenseSplit = sentense.split(' ');
        correctArr = sentenseSplit.slice(0);// 
        shuffleWords = _shuffle(sentenseSplit.map((item: string, i: number): TWord => {
            return {
                word: item,
                oriIdx: i,
            }
        }));
    }

    const [submitIdx, setSubmitIdx] = useState(0);
    const [submitArr, setSubmitArr] = useState<string[]>([]);
    const [hintIdx, setHintIdx] = useState(-1);
    const [message, setMessage] = useState('');
    const [isComplete, setIsComplete] = useState(false);

    const blanks = sentenseSplit.map((item: string, i: number) => {
        const submitWord = submitArr[i];
        return <div key={i} className="word-blank"><span className={classnames(["word-field", { "is-correct": submitWord }])}>{item}</span></div>
    })

    const words = shuffleWords.map((item: TWord, i: number) => {
        const { word, oriIdx } = item;
        const dpWord = (() => {
            return word.replace(/\,|\.|\-|\?/g, '').toLowerCase();
        })();
        return <button key={i} onClick={() => {
            //      
            if (correctArr[submitIdx].toLowerCase() === word.toLowerCase()) {
                // setMessage('');
                // setTimeout(() => {
                //     setMessage('');
                // }, 2000)
            } else {
                setMessage('Wrong~!');
                setTimeout(() => {
                    setMessage('');
                }, 1500)
                return;
            }

            const cloneArr: string[] = submitArr.slice(0);
            cloneArr[submitIdx] = word;
            setSubmitArr(cloneArr);
            setSubmitIdx(submitIdx + 1);
            if (correctArr.length === cloneArr.length) {
                setMessage('Complete!!');
                setIsComplete(true);
                setTimeout(() => {
                    setMessage('');
                }, 2000)
            }
        }} className={classnames(["word", { "shake": hintIdx === oriIdx }, { "is-done": submitIdx > oriIdx }])}><span className="word-field">{dpWord}</span></button>
    });

    return (
        <div className="MDictation">
            <h2></h2>
            {/* <div>
                {sentense}
            </div> */}

            <div className="blanks">
                {blanks}
            </div>
            <div className="sentenseKo">
                {sentenseKo}
            </div>
            <div className="words">
                {!isComplete && words}
            </div>
            <div className="ditaction-func">
                <button onClick={() => {
                    setHintIdx(submitIdx);
                    setTimeout(() => {
                        setHintIdx(-1);
                    }, 1000)
                }} className="btn btn-hint"><i className="fas fa-search"></i>Hint</button>
                {submitArr.length > 0 && <button onClick={() => {
                    setSubmitArr([]);
                    setSubmitIdx(0);
                    setHintIdx(-1);
                    setMessage('');
                    setIsComplete(false);
                }} className="btn btn-restart"><i className="fas fa-redo-alt"></i>Reset</button>}
            </div>
            {message !== '' && <div onClick={() => {
                setMessage('');
            }} className="message message-ani">
                <div className="message-box">
                    <div>
                        <div>{message}</div>
                        {message.substr(0, 8) === 'Complete' && <div className="lets-shadowing">Let's shadowing!!</div>}
                    </div>

                </div>
            </div>}
        </div>
    );
};

export default MDictation